@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);

.flex {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  grid-gap: 3rem;
  gap: 3rem;
}
.flex2 {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  grid-gap: 3rem;
  gap: 3rem;
  margin-bottom: 1.5rem;
}
.flex2 .feature__title {
  font-size: var(--font-med);
}
.flex2 .feature__text {
  font-size: var(--font-sml);
}
.feature__title {
  min-width: 30%;
  position: relative;
  margin-bottom: 2rem;
  color: #fff;
  font-weight: 700;
  font-size: var(--font-lg);
}

.feature__title::before {
  content: "";
  position: absolute;
  top: -1.5rem;
  left: 0;
  width: 2.5rem;
  height: 0.3rem;
  background: var(--gradient-bar);
}

.feature__text {
  margin-top: 0;
  margin-bottom: 2rem;
}

.features {
  display: flex;
  align-items: flex-start;
  grid-gap: 2rem;
  gap: 2rem;
}
.features__heading {
  flex: 2 1;
}

.features__heading a {
  display: block;
  color: var(--color-subtext);
  font-weight: 500;
  font-size: 1rem;
  margin-top: 2rem;
  cursor: pointer;
}

.features__content {
  flex: 3 1;
  margin-left: 3rem;
}

/* Media queries */
@media screen and (max-width: 1050px) {
  .features {
    flex-direction: column;
    grid-gap: 6rem;
    gap: 6rem;
  }
  .features__content {
    margin-left: 0;
  }
}

@media screen and (max-width: 700px) {
}

@media screen and (max-width: 550px) {
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--color-footer);
  color: #fff;
}

.footer__heading {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}
.footer__btn button {
  background: none;
  font-size: 1rem;
  border: 1px solid #ffffff;
  border-radius: 0;
}
.footer__content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  margin: 5rem 10rem 2rem;
  text-align: left;
}
.footer__content p {
  font-size: 0.9rem;
}
.footer__logo {
  width: 10.5rem;
  margin-right: 10rem;
}
.footer__links {
  width: 10rem;
  margin-left: 5rem;
}
.footer__copyright {
  text-align: center;
  font-size: var(--font-sml);
}
.footer__copyright p {
  font-size: var(--font-sml);
}

/* Media queries */
@media screen and (max-width: 1050px) {
}

@media screen and (max-width: 700px) {
}

@media screen and (max-width: 550px) {
  .footer__content {
    flex-direction: column;
  }
  .footer__logo {
    width: 100%;
    margin-right: 0;
  }
  .footer__links {
    width: 100%;
    margin-left: 1rem;
    margin-top: 3rem;
  }
}

.hero {
  display: flex;
}

.hero__content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-right: 5rem;
}

.hero__input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}

.hero__input input {
  flex: 2 1;
  width: 100%;
  min-height: 3.125rem;
  padding: 0 1rem;
  background: var(--color-footer);

  font-family: var(--font-family);
  font-size: var(--font-med);
  color: #fff;

  border-radius: 5px 0px 0px 5px;
  border: 2px solid var(--color-footer);
  outline: none;
}

.hero__input input::-webkit-input-placeholder {
  color: #3d6184;
}

.hero__input input:-ms-input-placeholder {
  color: #3d6184;
}

.hero__input input::placeholder {
  color: #3d6184;
}

.hero__input button {
  flex: 1 1;
  border-radius: 0px 5px 5px 0px;
}

.hero__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.hero__people span {
  margin-left: 1rem;
  font-weight: 500;
  font-size: var(--font-sml);
  color: #ffffff;
}

.hero__image {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero__image img {
  width: 100%;
  max-height: 100%;
}

/* media queries */
@media screen and (max-width: 1050px) {
  .hero {
    flex-direction: column;
  }

  .hero__content {
    margin: 0 0 3rem;
  }
}
@media screen and (max-width: 650px) {
  .hero__people {
    flex-direction: column;
  }
  .hero__people span {
    text-align: center;
    margin-top: 0.6rem;
  }
}

.possibility {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 6rem;
  gap: 6rem;
}
.possibility__image {
  display: flex;
  justify-content: center;
  align-items: center;
}
.possibility__image img {
  width: 40rem;
}
.possibility__content {
  align-self: flex-end;
}
.possibility__content h2 {
  margin-top: 2rem;
}
.possibility__content p {
  margin-bottom: 2rem;
}
.possibility__content a {
  color: #71e5ff;
  font-size: 1rem;
}
.possibility__content a:last-child {
  color: var(--color-subtext);
}

/* Media queries */
@media screen and (max-width: 1050px) {
  .possibility {
    flex-direction: column;
    grid-gap: 3rem;
    gap: 3rem;
  }
  .possibility__image img {
    width: 30rem;
  }
}

@media screen and (max-width: 700px) {
}

@media screen and (max-width: 550px) {
  .possibility__image img {
    width: 20rem;
  }
}

.about__quote {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4rem 0 0;
}

.about__quote h2 {
  max-width: 40rem;
}
.about__quote a {
  display: block;
  color: var(--color-subtext);
  font-weight: 500;
  font-size: 1rem;
  cursor: pointer;
}

.about__content {
  display: flex;
  grid-gap: 2.5rem;
  gap: 2.5rem;
  margin: 6rem 0 0;
}

/* Media queries */
@media screen and (max-width: 1050px) {
  .about__quote {
    flex-direction: column;
    align-items: start;
    grid-gap: 1.7rem;
    gap: 1.7rem;
  }
  .about__content {
    flex-direction: column;
  }
}

@media screen and (max-width: 700px) {
}

@media screen and (max-width: 550px) {
  .about__quote a {
    font-weight: 400;
    font-size: 0.8rem;
  }
}


.brand {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.brand div {
  flex: 1 1;
  max-width: 9.375rem;
  min-width: 7.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 4rem;
  background: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10.7236px;
  color: #000;
}

.cta__content a {
  font-size: 1rem;
  font-weight: 500;
}

.cta__content h3 {
  margin-top: 1rem;
}

.cta__btn button {
  padding: 0.8rem 2.8rem;
  background-color: #000000;
  color: #fff;
  border-radius: 40px;
}

@media screen and (max-width: 550px) {
  .cta {
    flex-direction: column;
    align-items: center;
    padding: 1rem;
  }
  .cta__btn button {
    margin-top: 2rem;
  }
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}

/* mixed */
.navbar__links p,
.navbar__sign p,
.navbar__menu p {
  color: #fff;
  font-weight: 500;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}

/* links */
.navbar__content {
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.navbar__logo {
  margin-right: 2rem;
}
.navbar__logo img {
  width: 4.5rem;
  height: 1.3rem;
}
.navbar__links {
  display: flex;
  flex-direction: row;
}

/* sign */
.navbar__sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.navbar__sign button {
  margin-left: 1rem;
}

/* menu */
.navbar__menu {
  display: none;
  position: relative;
  margin-left: 1rem;
  cursor: pointer;
}

.navbar__menu-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;

  min-width: 210px;
  position: absolute;
  top: 2.5rem;
  right: 0;
  background: var(--color-footer);
  margin-top: 1rem;
  padding: 2rem;
  text-align: end;
  border-radius: 5px;
  box-shadow: 0 0 5 rdba(0, 0, 0, 0.2);
}
.navbar__menu-content p {
  margin: 1rem 0;
}

.navbar__menu-sign {
  display: none;
}

/* Media queries */
@media screen and (max-width: 1050px) {
  .navbar__links {
    display: none;
  }
  .navbar__menu {
    display: flex;
  }
}

@media screen and (max-width: 700px) {
  .navbar {
    padding: 2rem 4rem;
  }
}

@media screen and (max-width: 550px) {
  .navbar {
    padding: 2rem;
  }
  .navbar__sign {
    display: none;
  }
  .navbar__menu-content {
    top: 20px;
  }
  .navbar__menu-sign {
    display: block;
  }
}

/* Global styles */

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family);
  font-size: var(--font-med);
  font-weight: 300;
  line-height: 1.3;
  background: var(--color-bg);
  color: var(--color-text);
  overflow-x: hidden;
}

/* text */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: 800;
}

h1 {
  font-size: var(--font-xxl);
  line-height: 4.9rem;
  letter-spacing: -0.04em;
}
h2 {
  font-size: var(--font-xl);
}

p {
  font-size: var(--font-med);
  line-height: 27px;
  margin-top: 1.5rem;
}

a,
a:visited,
a:hover {
  color: unset;
  text-decoration: none;
}

@media screen and (max-width: 1050px) {
}

@media screen and (max-width: 650px) {
  h1 {
    font-size: var(--font-xl);
    line-height: 3.75rem;
  }
  h2 {
    font-size: var(--font-lg2);
  }

  p {
    font-size: var(--font-med);
    line-height: 1.5rem;
  }
}

/* spacing */

.container-pd {
  padding: 4rem 6rem;
}

.container-mg {
  margin: 4rem 6rem;
}

@media screen and (max-width: 1050px) {
  .container-pd {
    padding: 2rem 3rem;
  }

  .container-mg {
    margin: 4rem;
  }
}

@media screen and (max-width: 650px) {
  .container-pd {
    padding: 1rem 1.5rem;
  }
  .container-mg {
    margin: 2rem 1.5rem;
  }
}

/* elements */
.button {
  padding: 0.5rem 1rem;
  background-color: #ff4820;
  color: #fff;
  font-weight: 500;
  font-size: var(--font-med);
  line-height: 25px;
  border: 0;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

/* gradients */

.gradient__bg {
  background: radial-gradient(
    circle at 3% 25%,
    rgba(0, 40, 83, 1) 0%,
    rgba(4, 12, 24, 1) 99%
  );
}

.gradient__bg--light {
  background: -o-linear-gradient(
    145deg,
    rgba(27, 137, 222, 1) 0%,
    rgba(4, 44, 84, 1) 10%,
    rgba(4, 44, 84, 1) 75%,
    rgba(27, 120, 222, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1B78DE', endColorstr='#1B89DE', GradientType=1 );
  background: linear-gradient(
    145deg,
    rgba(27, 137, 222, 1) 0%,
    rgba(4, 44, 84, 1) 10%,
    rgba(4, 44, 84, 1) 75%,
    rgba(27, 120, 222, 1) 100%
  );
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Animations */
.scale-up-center {
  animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

/* Imports and global custom variables */

:root {
  --font-family: "Manrope", sans-serif;

  --gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%);

  --color-bg: #040c18;
  --color-footer: #031b34;
  --color-blog: #042c54;
  --color-text: #81afdd;
  --color-subtext: #ff8a71;

  /* 14px, 18px, 20px, 52px, 62px */
  --font-sml: 0.875rem;
  --font-med: 1.125rem;
  --font-lg: 1.25rem;
  --font-lg2: 2rem;
  --font-xl: 3rem;
  --font-xxl: 3.975rem;
}

